<template>
  <article class="price-pop">
    <van-form @submit="onSubmit">
      <van-cell-group inset>
        <van-field
          v-model="price"
          name="price"
          label=""
          placeholder=""
          autocomplete="off"
          type="number"
          :rules="[{ pattern, message: '2,0' }]"
        />
        <van-field
          v-model="oldPrice"
          name="oldPrice"
          label=""
          placeholder=""
          autocomplete="off"
          type="number"
          :rules="[{ pattern, message: '2,0' }]"
        />
        <van-field
          v-model="mailCharge"
          name="mailCharge"
          label=""
          type="number"
          placeholder=""
          autocomplete="off"
          :rules="[{ validator, message: '2,0' }]"
        >
          <template v-slot:extra>
            <van-checkbox v-model="isMailCharge" :disabled="isDisabled == 1" @change="mailChargeChange"
              ></van-checkbox
            >
          </template></van-field
        >
      </van-cell-group>
      <div class="button">
        <van-button round block type="primary" native-type="submit" size="normal" color="#2e9aff">  </van-button>
      </div>
    </van-form>
  </article>
</template>

<script lang="ts">
import { ref, watch, defineComponent, watchEffect } from 'vue';
export default defineComponent({
  props: {
    allPrice: {
      type: Object,
      default: () => {
        return {};
      }
    },
    isShowMail: {
      type: Boolean,
      default: false
    }
  },
  emits: ['setPriceData'],
  setup(props, { emit }) {
    const checked = ref(false);
    const isDisabled = ref(0);
    const blinkShow = ref(0);
    const isMailCharge = ref(false);
    const price = ref('');
    const oldPrice = ref('');
    const mailCharge = ref('');
    if (props.allPrice) {
      price.value = props.allPrice.price == 0 ? '' : props.allPrice.price;
      oldPrice.value = props.allPrice.oldPrice == 0 ? '' : props.allPrice.oldPrice;
      mailCharge.value = props.allPrice.mailCharge == 0 ? '' : props.allPrice.mailCharge;
      if (props.allPrice.mailCharge === '') {
        isMailCharge.value = true;
      }
    }
    //
    watchEffect(() => {
      if (props.isShowMail) {
        isMailCharge.value = true;
        isDisabled.value = 1;
      } else {
        isMailCharge.value = false;
        isDisabled.value = 0;
      }
    });
    watch(isMailCharge, (newValue) => {
      if (newValue) {
        mailCharge.value = '';
      } else {
        mailCharge.value = '';
      }
    });
    const mailChargeChange = (checked: boolean) => {
      if (checked) {
        mailCharge.value = '';
      } else {
        mailCharge.value = '';
      }
    };
    const pattern = /(?:^[1-9]([0-9]+)?(?:\.[0-9]{1,2})?$)|(?:^(?:0)$)|(?:^[0-9]\.[0-9](?:[0-9])?$)/;
    const validator = (val: any) => {
      if (val === '') {
        return true;
      } else {
        return /(?:^[1-9]([0-9]+)?(?:\.[0-9]{1,2})?$)|(?:^(?:0)$)|(?:^[0-9]\.[0-9](?:[0-9])?$)/.test(val);
      }
    };
    const onSubmit = () => {
      const allPrices = {
        price: price.value,
        oldPrice: oldPrice.value,
        mailCharge: mailCharge.value
      };
      emit('setPriceData', allPrices);
    };
    // watch(mailCharge, (newValue) => {
    //   console.log(newValue);
    //   if (newValue !== '') {
    //     isMailCharge.value = false;
    //   } else {
    //     isMailCharge.value = true;
    //   }
    // });
    const mailChargeBlur = (e: any) => {
      if (e.target.value !== '') {
        isMailCharge.value = false;
      }
    };
    return {
      blinkShow,
      price,
      oldPrice,
      mailCharge,
      isMailCharge,
      isDisabled,
      checked,
      mailChargeChange,
      onSubmit,
      mailChargeBlur,
      pattern,
      validator
    };
  }
});
</script>

<style lang="scss" scoped>
.price-pop {
  .button {
    margin-top: 20px;
    margin-bottom: 10px;
    width: 70%;
    margin-left: 50%;
    transform: translateX(-50%);
  }
}
</style>
